/* eslint-disable no-inner-declarations */
var acorn = require('acorn'); // javascript parser
// eslint-disable-next-line import/newline-after-import


var jsx = require('acorn-jsx');

var JSXParser = acorn.Parser.extend(jsx()); // Helper function to grab the getters/setters from `elementType`

module.exports = function (elementType) {
  // Initialize empty object to store the setters and getter
  var ast = JSXParser.parse(elementType);
  var hookState = {};

  var _loop = function _loop() {
    ast = ast.body;
    var statements = [];
    /** All module exports always start off as a single 'FunctionDeclaration' type
     * Other types: "BlockStatement" / "ExpressionStatement" / "ReturnStatement"
     * Iterate through AST of every function declaration
     * Check within each function declaration if there are hook declarations */

    ast.forEach(function (functionDec) {
      var body;
      if (functionDec.expression) body = functionDec.expression.body.body;else body = functionDec.body.body; // Traverse through the function's funcDecs and Expression Statements

      body.forEach(function (elem) {
        if (elem.type === 'VariableDeclaration') {
          elem.declarations.forEach(function (hook) {
            statements.push(hook.id.name);
          });
        }
      });
      /* body will look something like:
      [ 0: "_useState"
        1: "_useState2"
        2: "character"
        3: "setCharacter" ]
      */
      // Iterate array and determine getter/setters based on pattern

      statements.forEach(function (el, i) {
        if (el.match(/_use/)) hookState[el] = statements[i + 2];
      });
    });
  };

  while (Object.hasOwnProperty.call(ast, 'body')) {
    _loop();
  }

  return hookState;
};